// app/projects/[projectId]/page.js
"use client";

import React, { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import {
  Maximize2,
  ArrowLeft,
  X,
  Plus,
  Edit2,
  Trash2,
  User,
  Calendar,
  CheckCircle2,
  Circle,
  ChevronRight,
  LayoutDashboard,
  CheckSquare,
} from "lucide-react";
import { format } from "date-fns";

import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { toast } from "sonner";

export default function ProjectPage() {
  const router = useRouter();
  const { projectId, slug = [] } = useParams();
  const slugArray = Array.isArray(slug) ? slug : [slug];

  const tab = slugArray[0] || "dashboard"; // "dashboard" | "tasks"
  const taskId = slugArray[1] ? Number(slugArray[1]) : null;
  const action = slugArray[2]; // "create" | "edit"

  // Tasks Data
  const [tasks, setTasks] = useState([
    { id: 1, title: "Design new landing page", description: "Bold hero with smooth animations", status: "In Progress", assignee: "Sarah", dueDate: "2025-12-10", priority: "High" },
    { id: 2, title: "Mobile navigation fix", description: "Hamburger menu not closing", status: "Open", assignee: "Mike", dueDate: null, priority: "High" },
    { id: 3, title: "Dark mode toggle", description: "Auto + manual switch", status: "Done", assignee: "You", dueDate: "2025-11-28", priority: "Medium" },
    { id: 4, title: "Analytics dashboard", description: "Realtime charts", status: "In Progress", assignee: "Alex", dueDate: "2025-12-05", priority: "Medium" },
  ]);

  // Current viewed/edited task
  const [currentTask, setCurrentTask] = useState(null);
  const [isEditing, setIsEditing] = useState(false);

  // Sync URL â†’ State
  useEffect(() => {
    if (tab === "dashboard") {
      setCurrentTask(null);
      setIsEditing(false);
    } else if (action === "create") {
      setCurrentTask({});
      setIsEditing(true);
    } else if (taskId) {
      const task = tasks.find(t => t.id === taskId);
      setCurrentTask(task || null);
      setIsEditing(action === "edit");
    } else {
      setCurrentTask(null);
      setIsEditing(false);
    }
  }, [tab, taskId, action, tasks]);

  const goTo = (path) => router.push(`/projects/${projectId}/${path}`);

  const saveTask = (e) => {
    e.preventDefault();
    const fd = new FormData(e.target);
    const data = Object.fromEntries(fd);

    if (!currentTask?.id) {
      // Create
      const newId = Math.max(...tasks.map(t => t.id), 0) + 1;
      setTasks(prev => [{ id: newId, ...data, priority: data.priority || "Medium" }, ...prev]);
      toast.success("Task created âœ¨");
      goTo(`tasks/${newId}`);
    } else {
      // Update
      setTasks(prev => prev.map(t => t.id === currentTask.id ? { ...t, ...data } : t));
      toast.success("Task saved! ðŸŽ‰");
      setIsEditing(false);
      goTo(`tasks/${currentTask.id}`);
    }
  };

  const deleteTask = () => {
    setTasks(prev => prev.filter(t => t.id !== currentTask.id));
    toast.success("Task deleted");
    goTo("tasks");
  };

  const getStatusBadge = (status) => {
    const colors = {
      "Done": "bg-emerald-100 text-emerald-700 border-emerald-200",
      "In Progress": "bg-blue-100 text-blue-700 border-blue-200",
      "Open": "bg-gray-100 text-gray-700 border-gray-200",
    };
    return colors[status] || colors["Open"];
  };

  const getPriorityBadge = (priority) => {
    const colors = {
      "High": "bg-red-100 text-red-700",
      "Medium": "bg-amber-100 text-amber-700",
      "Low": "bg-gray-100 text-gray-600",
    };
    return colors[priority] || colors["Medium"];
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-indigo-100">
      {/* Top Navigation Tabs */}
      <div className="sticky top-0 bg-white/80 backdrop-blur-lg border-b border-gray-200 z-50">
        <div className="max-w-7xl mx-auto px-8 py-6 flex items-center justify-between">
          <div className="flex items-center gap transports-24">
            <h1 className="text-3xl font-bold text-gray-900">Project {projectId}</h1>
            <div className="flex gap-1 ml-12">
              <Button
                variant={tab === "dashboard" ? "default" : "ghost"}
                size="lg"
                onClick={() => goTo("dashboard")}
                className="rounded-xl"
              >
                <LayoutDashboard className="w-5 h-5 mr-2" />
                Dashboard
              </Button>
              <Button
                variant={tab === "tasks" ? "default" : "ghost"}
                size="lg"
                onClick={() => goTo("tasks")}
                className="rounded-xl"
              >
                <CheckSquare className="w-5 h-5 mr-2" />
                Tasks <span className="ml-2 text-sm font-bold">({tasks.length})</span>
              </Button>
            </div>
          </div>

          {tab === "tasks" && !currentTask && (
            <Button onClick={() => goTo("tasks/create")} className="bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-700 hover:to-purple-700 shadow-lg">
              <Plus className="w-5 h-5 mr-2" /> New Task
            </Button>
          )}
        </div>
      </div>

      {/* Main Content Area - Full Width */}
      <div className="max-w-7xl mx-auto px-8 py-10">
        {/* DASHBOARD VIEW */}
        {tab === "dashboard" && (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            <Card className="p-8 bg-gradient-to-br from-indigo-500 to-purple-600 text-white shadow-xl">
              <h3 className="text-5xl font-bold">{tasks.length}</h3>
              <p className="text-indigo-100 mt-2 text-lg">Total Tasks</p>
            </Card>
            <Card className="p-8 bg-gradient-to-br from-emerald-500 to-teal-600 text-white shadow-xl">
              <h3 className="text-5xl font-bold">{tasks.filter(t => t.status === "Done").length}</h3>
              <p className="text-emerald-100 mt-2 text-lg">Completed</p>
            </Card>
            <Card className="p-8 bg-gradient-to-br from-amber-500 to-orange-600 text-white shadow-xl">
              <h3 className="text-5xl font-bold">{tasks.filter(t => t.priority === "High").length}</h3>
              <p className="text-amber-100 mt-2 text-lg">High Priority</p>
            </Card>
            <Card className="p-8 bg-gradient-to-br from-pink-500 to-rose-600 text-white shadow-xl">
              <h3 className="text-5xl font-bold">{tasks.filter(t => t.status === "In Progress").length}</h3>
              <p className="text-pink-100 mt-2 text-lg">In Progress</p>
            </Card>
          </div>
        )}

        {/* TASKS LIST VIEW */}
        {tab === "tasks" && !currentTask && (
          <div className="space-y-6">
            {tasks.map(task => (
              <Card
                key={task.id}
                className="p-8 cursor-pointer transition-all hover:shadow-2xl hover:scale-[1.02] border-2 border-transparent hover:border-indigo-300"
                onClick={() => goTo(`tasks/${task.id}`)}
              >
                <div className="flex justify-between items-start">
                  <div>
                    <h3 className="text-2xl font-bold text-gray-900">{task.title}</h3>
                    <div className="flex flex-wrap items-center gap-4 mt-5 text-sm">
                      <Badge className={getStatusBadge(task.status)}>
                        {task.status === "Done" ? <CheckCircle2 className="w-4 h-4 mr-1" /> : <Circle className="w-4 h-4 mr-1" />}
                        {task.status}
                      </Badge>
                      <Badge className={getPriorityBadge(task.priority)}>{task.priority}</Badge>
                      <span className="flex items-center gap-2 text-gray-600">
                        <User className="w-4 h-4" /> {task.assignee}
                      </span>
                      {task.dueDate && (
                        <span className="flex items-center gap-2 text-gray-600">
                          <Calendar className="w-4 h-4" /> {format(new Date(task.dueDate), "MMM d, yyyy")}
                        </span>
                      )}
                    </div>
                  </div>
                  <ChevronRight className="w-8 h-8 text-indigo-400" />
                </div>
              </Card>
            ))}
          </div>
        )}

        {/* TASK DETAIL / CREATE / EDIT - Same Panel */}
        {currentTask && (
          <div className="bg-white rounded-3xl shadow-2xl p-10 max-w-4xl mx-auto animate-in slide-in-from-right duration-500">
            <div className="flex justify-between items-center mb-10">
              <h1 className="text-4xl font-bold text-gray-900">
                {currentTask.id ? currentTask.title : "New Task"}
              </h1>
              <div className="flex gap-3">
                {currentTask.id && !isEditing && (
                  <Button onClick={() => goTo(`tasks/${taskId}/edit`)} variant="outline" size="lg">
                    <Edit2 className="w-5 h-5 mr-2" /> Edit
                  </Button>
                )}
                <Button variant="ghost" size="icon" onClick={() => goTo("tasks")}>
                  <X className="w-6 h-6" />
                </Button>
              </div>
            </div>

            {isEditing || !currentTask.id ? (
              <form onSubmit={saveTask} className="space-y-10">
                <div>
                  <Label className="text-xl font-semibold">Title</Label>
                  <Input name="title" defaultValue={currentTask.title || ""} required className="mt-3 text-2xl h-16" placeholder="Amazing task title..." />
                </div>

                <div>
                  <Label className="text-xl font-semibold">Description</Label>
                  <Textarea name="description" defaultValue={currentTask.description || ""} rows={8} className="mt-3 text-lg" placeholder="What should be done?" />
                </div>

                <div className="grid grid-cols-2 gap-8">
                  <div>
                    <Label className="text-xl font-semibold">Status</Label>
                    <Select name="status" defaultValue={currentTask.status || "Open"}>
                      <SelectTrigger className="mt-3 h-14 text-lg"><SelectValue /></SelectTrigger>
                      <SelectContent>
                        <SelectItem value="Open">Open</SelectItem>
                        <SelectItem value="In Progress">In Progress</SelectItem>
                        <SelectItem value="Done">Done</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <Label className="text-xl font-semibold">Priority</Label>
                    <Select name="priority" defaultValue={currentTask.priority || "Medium"}>
                      <SelectTrigger className="mt-3 h-14 text-lg"><SelectValue /></SelectTrigger>
                      <SelectContent>
                        <SelectItem value="Low">Low</SelectItem>
                        <SelectItem value="Medium">Medium</SelectItem>
                        <SelectItem value="High">High</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  <div>
                    <Label className="text-xl font-semibold">Assignee</Label>
                    <Input name="assignee" defaultValue={currentTask.assignee || ""} className="mt-3 h-14 text-lg" />
                  </div>
                  <div>
                    <Label className="text-xl font-semibold">Due Date</Label>
                    <Input type="date" name="dueDate" defaultValue={currentTask.dueDate || ""} className="mt-3 h-14 text-lg" />
                  </div>
                </div>

                <Separator className="my-10" />

                <div className="flex justify-between">
                  <Button type="button" variant="destructive" size="lg" onClick={currentTask.id ? deleteTask : (() => goTo("tasks"))}>
                    <Trash2 className="w-5 h-5 mr-2" />
                    {currentTask.id ? "Delete Task" : "Cancel"}
                  </Button>
                  <Button type="submit" size="lg" className="bg-gradient-to-r from-emerald-600 to-teal-600 hover:from-emerald-700 hover:to-teal-700 px-10 shadow-xl">
                    <CheckCircle2 className="w-6 h-6 mr-3" />
                    {currentTask.id ? "Save Changes" : "Create Task"}
                  </Button>
                </div>
              </form>
            ) : (
              /* VIEW MODE */
              <div className="space-y-12">
                <div className="flex gap-6">
                  <Badge className={`text-xl px-6 py-3 ${getStatusBadge(currentTask.status)}`}>
                    {currentTask.status}
                  </Badge>
                  <Badge className={`text-xl px-6 py-3 ${getPriorityBadge(currentTask.priority)}`}>
                    {currentTask.priority} Priority
                  </Badge>
                </div>

                <div className="grid grid-cols-2 gap-10 text-lg">
                  <div>
                    <Label className="text-gray-600 text-xl">Assignee</Label>
                    <p className="text-3xl font-bold mt-3">{currentTask.assignee}</p>
                  </div>
                  {currentTask.dueDate && (
                    <div>
                      <Label className="text-gray-600 text-xl">Due Date</Label>
                      <p className="text-3xl font-bold mt-3">{format(new Date(currentTask.dueDate), "MMMM d, yyyy")}</p>
                    </div>
                  )}
                </div>

                <div>
                  <Label className="text-gray-600 text-xl">Description</Label>
                  <p className="text-xl leading-relaxed mt-4 text-gray-700 whitespace-pre-wrap">
                    {currentTask.description || "No description yet."}
                  </p>
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}